import React from 'react'
import { connect } from 'react-redux'
import { withTranslation } from 'react-i18next'

const TOTAL_SUPPLY = 20000000
const MILIION = 1000000

class Index extends React.Component {
  componentDidMount () {}
  render () {
    const { t } = this.props
    return (
      <div className='dashboard-session dashboard-market-info'>
        <div className='ui header session-header'>{t('marketInfo.title')}</div>
        <div className='info-card-group'>
          <div className='info-card'>
            <div className='ui header'>{t('marketInfo.items.reserve')}</div>
            <div className='content'>{Number(this.props.marketInfo.reserveBalanceHodlc / MILIION).toFixed(2)}M HODLC</div>
          </div>
          <div className='info-card'>
            <div className='ui header'>{t('marketInfo.items.marketcap')}</div>
            <div className='content'>${Number(TOTAL_SUPPLY * this.props.marketInfo.rates / MILIION).toFixed(2)}M</div>
          </div>
        </div><div className='info-card-group'>
        <div className='info-card'>
          <div className='ui header'>{t('marketInfo.items.circulation')}</div>
          <div className='content'>{Number((TOTAL_SUPPLY - this.props.marketInfo.reserveBalanceHodlc) / MILIION).toFixed(2)}M HODLC (${Number((TOTAL_SUPPLY - this.props.marketInfo.reserveBalanceHodlc) / MILIION * this.props.marketInfo.rates).toFixed(2)}M)</div>
        </div>
        <div className='info-card'>
          <div className='ui header'>{t('marketInfo.items.hodlcPrice')}</div>
          <div className='content'>${Number(this.props.marketInfo.rates).toFixed(4)}</div>
        </div>
      </div>
      </div>
    )
  }
}

const mapStateToProps = ({ wallet, trade, blockchain }) => {
  return {
    marketInfo: {
      reserveBalanceHodlc: trade.reserveBalanceHodlc,
      rates: trade.rates,
    },
  }
}

export default connect(mapStateToProps)(withTranslation()(Index))

