import { CANCEL_BUY, CANCEL_SELL, GET_USER_BALANCES, GET_DEX_DATA, INIT_BLOCKCHAIN_ENVIRONMENT } from '../types'
import connector from '../../utils/connector'
import blockchain from './blockchain'

export const initBlockchainEnvironment = (amount) => async dispatch => {
  try {
    const data = await blockchain.initBlockchainEnvironment(amount)
    dispatch({
      type: INIT_BLOCKCHAIN_ENVIRONMENT,
      payload: data
    })
  } catch (err) {
    console.error(err)
  }
}

export const connectWithMetaMask = (b) => async dispatch => {
  try {
    await blockchain.connectWithMetaMask(b)
  } catch (err) {
    console.error(err)
  }
}

