import React, { Component } from 'react'

import { connect } from 'react-redux'

// Install Toastify
import { ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'

import { getUserBalances, getDexData, initBlockchainEnvironment } from '../redux/actions'
import Router from './Router'
import Navigator from '../components/Navs/Navigator'
import connector from '../utils/connector'
import Navbar from '../components/Navs/Navbar'
// import Info from '../components/Navs/bottom'
import config from '../config'

class App extends Component {
  componentWillUnmount () {
    clearInterval(this.myinterval)
    clearInterval(this.myinterval2)
  }

  componentDidMount () {
    // init blockchain environment
    this.props.initBlockchainEnvironment()
    // init
    setTimeout(() => {
      this.props.getUserBalances(this.props.web3, this.props.account)
      this.props.getDexData()
    }, 500)
    try {
      this.myinterval = setInterval(() => {
        this.props.getUserBalances(this.props.web3, this.props.account)
      }, config.rqstInteval.balance) // refresh wallet balance every 10 seconds
    } catch (e) {
      console.log(e)
    }
    try {
      this.myinterval2 = setInterval(() => {
        this.props.getDexData()
      }, config.rqstInteval.dexData) // request dex data every 5 minutes
    } catch (e) {
      console.log(e)
    }
  }

  async componentWillMount () {
    await connector.getDexData(this.props.web3)
  }

  render () {
    return (
      <div>
        <Navigator />
        <Navbar />
        <Router />
        <ToastContainer
          position='bottom-right'
          autoClose={3000}
          hideProgressBar={false}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover
        />
        {/*<Info />*/}
      </div>
    )
  }
}

const mapStateToProps = ({ blockchain }) => {
  return {
    account: blockchain.account,
    web3: blockchain.web3
  }
}

export default connect(mapStateToProps, { getUserBalances, getDexData, initBlockchainEnvironment })(App)
