import React from "react";
import { connect } from 'react-redux';

import config from '../../config';
import OrderBook from '../../utils/connector/orderBook';
import List from './list';

const INIT_STATE = {
  book: [],
  total: 0,
  type: ''
}

class Book extends React.Component {

  state = INIT_STATE

  componentWillUnmount(){
    clearInterval(this.myinterval);
  }

  componentDidMount(){
    this.initBook();
    try { 
      this.myinterval = setInterval( () => {
        this.updateBook();
      }, config.rqstInteval.order); 
    } catch(e) {
      console.log(e);
    }
  }

  updateBook = () => {
    const book = new OrderBook(this.props.web3);
    book.removeAllListeners();
    let temp = []
    book.on('init', () => {
      temp = [];
    })
    // book.on('total', (payload)=>{
    //   this.setState({total: payload})
    // })
    book.on('firstOrder', (payload)=>{
      temp = [payload];
    })
    book.on('newOrder', (payload)=>{
      temp.push(payload);
    })
    book.on('done', (payload)=>{
      this.setState({ book: temp, total: temp.length })
    })
    book.subscribe(this.props.type, this.props.user)
  }

  initBook = () => {
    const book = new OrderBook(this.props.web3);
    book.removeAllListeners();
    book.on('init', () => {
      this.setState(INIT_STATE)
    })
    book.on('total', (payload)=>{
      this.setState({total: payload})
    })
    book.on('firstOrder', (payload)=>{
      this.setState({ book: [payload] })
    })
    book.on('newOrder', (payload)=>{
      this.setState({ book: [...this.state.book, payload], total: this.state.book.length+1 })
    })
    book.subscribe(this.props.type, this.props.user)
  }

  componentDidUpdate(prevProps, prevState){
    if (prevProps.type !== this.props.type) return this.updateBook();
  }

  render() {
    return (
      <div className="list-container">
        <List type={this.props.type} orders={this.state.book} total={this.state.total || 0} />
      </div>
    )
  }
}

const mapStateToProps = ({ blockchain }) => {
  return {
    web3: blockchain.web3
  }
}

export default connect(mapStateToProps)(Book)
