import React from 'react'
import { connect } from 'react-redux'
import { withTranslation } from 'react-i18next'
import ConnectModal from '../Profile/ConnectModal'
import i18n from 'i18next'

const TOTAL_SUPPLY = 20000000
const MILIION = 1000000

const Loader = () => (<div className="loading-component navbar"><div className="sk-chase">
  <div className="sk-chase-dot"></div>
  <div className="sk-chase-dot"></div>
  <div className="sk-chase-dot"></div>
  <div className="sk-chase-dot"></div>
  <div className="sk-chase-dot"></div>
  <div className="sk-chase-dot"></div>
</div></div>)

class Navbar extends React.Component {
  state = {
    showModal: false,
    loadingWallet: true
  }

  componentDidMount () {
    this.myLoadingWallet = setTimeout(async () => {
      this.setState({loadingWallet: false});
    }, 1000);
  }

  hideModal = () => {
    this.setState({ showModal: false})
  }

  renderCurrentWallet = (account) => {
    const acc = account.length;
    if(account==='loading') {
      return <div className='item connect-item' onClick={() => this.setState({showModal: true})}>
        <Loader />
      </div>
    }else{
      if(acc > 10) {
        return <div className='item connect-item' onClick={() => this.setState({showModal: true})}>
          {account.slice(0, 5)}...{account.slice(acc-5, acc)}
        </div>
      }else{
        return <div className='item connect-item empty-wallet' onClick={() => this.setState({showModal: true})}>
          Connect to a wallet
        </div>
      }
    }
  }

  getCurrentLanguage = () => {
    return localStorage.getItem('i18nextLng');
  }

  render () {
    const { t } = this.props
    return (
      <div className='top-nav'>
        <div className='title'>HODL DEX</div>
        <div className='left-group'>
          <div className='item'>
            <div className='category'>{t('marketInfo.items.reserve')}</div>
            <div className='data'>{Number(this.props.reserveBalanceHodlc / MILIION).toFixed(2)}M HODLC</div>
          </div>
          <div className='item'>
            <div className='category'>{t('marketInfo.items.marketcap')}</div>
            <div className='data'>${Number(TOTAL_SUPPLY * this.props.rates / MILIION).toFixed(2)}M</div>
          </div>
          <div className='item'>
            <div className='category'>{t('marketInfo.items.circulation')}</div>
            <div className='data'>{Number((TOTAL_SUPPLY - this.props.reserveBalanceHodlc) / MILIION).toFixed(2)}M HODLC (${Number((TOTAL_SUPPLY - this.props.reserveBalanceHodlc) / MILIION * this.props.rates).toFixed(2)}M)</div>
          </div>
          <div className='item'>
            <div className='category'>{t('marketInfo.items.hodlcPrice')}</div>
            <div className='data'>${Number(this.props.rates).toFixed(4)}</div>
          </div>
        </div>
        <div className='right-group'>
        <div className='item'>
              <select id='language-dropdown' value={this.getCurrentLanguage()} className='language-dropdown' name="language" onChange={(e) =>  i18n.changeLanguage(e.target.value)}>
                  <option value="en">EN</option>
                  <option value="es">ES</option>
                  <option value="fr">FR</option>
                  <option value="zh_CN"></option>
                  <option value="zh_TW"></option>
              </select>
          </div>
          {this.renderCurrentWallet(this.props.account)}
          {this.state.showModal&&<ConnectModal hideModal={this.hideModal}/>}
         
        </div>
      </div>
    )
  }
}

const mapStateToProps = ({ trade, blockchain }) => {
  return {
    reserveBalanceHodlc: trade.reserveBalanceHodlc,
    rates: trade.rates,
    account: blockchain.account,
    web3: blockchain.web3
  }
}

export default connect(mapStateToProps)(withTranslation()(Navbar))
