import React from 'react'
import ReactDOM from 'react-dom'
import { connect } from 'react-redux'
import { connectWithMetaMask, connectWithWalletConnect } from '../../redux/actions'

class ConnectModal extends React.Component {
  render () {
    return ReactDOM.createPortal(
      <div className='ui dimmer modals visible active' onClick={() => this.props.hideModal()}>
        <div onClick={(e) => e.stopPropagation()} className='ui small modal visible active main-modal connect-modal'>
          <div className='ui top attached big label modal-title'>Choose your wallet</div>
          <div className='modal-body'>
            <div className='connect-group'>
              <div className={`item ${this.props.blockchain.metamask.connected && !this.props.blockchain.walletConnect.connected}`} onClick={() => this.props.connectWithMetaMask(this.props.blockchain)}>
                <div className='left-item-container'>
                  <div className='connected' />
                  <div className='title'>MetaMask</div>
                </div>
                <img alt='metamask' className='metamask-icon' src={require('../../assets/images/metamask.svg')} width='25px' />
              </div>
              <div className={`item ${this.props.blockchain.walletConnect.connected}`} onClick={() => this.props.connectWithWalletConnect(this.props.blockchain)}>
                <div className='left-item-container'>
                  <div className='connected' />
                  <div className='title'>WalletConnet</div>
                </div>
                <img alt='metamask' className='metamask-icon' src={require('../../assets/images/walletConnect.svg')} width='25px' />
              </div>
            </div>
          </div>
        </div>
      </div>,
      document.querySelector('#modal')
    )
  }
}

const mapStateToProps = ({ blockchain }) => {
  return {
    blockchain
  }
}

export default connect(mapStateToProps, { connectWithMetaMask, connectWithWalletConnect })(ConnectModal)
